class PM25API extends React.Component {

  constructor(props) { //Something like __init__
    super(props);
    this.state = {
      pm25: [],
    };

  }

  componentWillMount () {
    fetch("https://api.data.gov.sg/v1/environment/pm25") //Fetch data from webpage
    .then( (results) => {
      return results.json(); //return data in JSON (since its JSON data)
    }).then( (data) => {
      const finaldatas = data.items.map ( (finaldata) => { //map/loop through data.items(which is 1 part of the JSON data) and execute the function for each item
                                                           //(resulting data is passed through the parameter of the function)
      
      const updatetimearray =  finaldata.update_timestamp.split("T")
      const updatedate = updatetimearray[0]
      const updatetime = updatetimearray[1].slice(0,8)
      const finalupdatetime = updatedate + " " + updatetime

      return(
          <div key={finaldata.items}>
            <div id="MapContainer">
            <img id="map" src={require('./Map.jpg')} alt="Map" />
            <p>Last Updated: {finalupdatetime}</p>
             <p id="PM25West"> <mark>West: {finaldata.readings.pm25_one_hourly.west} </mark> </p> {/*Use dot operator to navigate through JSON data*/}
             <p id="PM25East"> <mark> East: {finaldata.readings.pm25_one_hourly.east} </mark> </p> 
             <p id="PM25Central"> <mark> Central: {finaldata.readings.pm25_one_hourly.central} </mark> </p> 
             <p id="PM25South"> <mark> South: {finaldata.readings.pm25_one_hourly.south} </mark> </p>
             <p id="PM25North"> <mark> North: {finaldata.readings.pm25_one_hourly.north} </mark> </p> 
            </div>
          </div>
        )
      })
      this.setState({pm25: finaldatas})
    })
  }

  render () {

    return (

      <div className="PM25API">
        <h2>Singapore PM2.5 Hourly Readings</h2>
        {this.state.pm25}
      </div>

    );
  }

}



class CarparkAPI extends React.Component {

  constructor(props) { //Something like __init__
    super(props);
    this.state = {
      carpark: [],
      lastupdate: "",
    };

  }

  componentWillMount () {
    fetch("https://api.data.gov.sg/v1/transport/carpark-availability") //Fetch data from webpage
    .then( (results) => {
      return results.json(); //return data in JSON (since its JSON data)
    }).then( (data) => {
      const timestamp = data.items[0].timestamp;
      const timestamparray = timestamp.split("T")
      const updatedate = timestamparray[0]
      const updatetime = timestamparray[1].slice(0, 8)
      const finalupdatetime = updatedate + " " + updatetime
      this.setState({lastupdate: finalupdatetime}) 
      const finaldatas = data.items[0].carpark_data.map ( (finaldata) => { //map/loop through data.items(which is 1 part of the JSON data) and execute the function for each item
                                                                          //(resulting data is passed through the parameter of the function)
        return(
          <div key={finaldata.carpark_data}>
            <p>Carpark Number: {finaldata.carpark_number}</p>
            <p>Carpark Lots: {finaldata.carpark_info[0].lots_available}</p>
          </div>
        );
      })

      this.setState({carpark: finaldatas})

    })
  }

  renderItem(index, key) { //Function for ReactList to render
    return <div key={key}>{this.state.carpark[index]}</div>;
  }

  render () {

    return (

      <div className="CarparkAPI">
        <h2>Singapore Carpark Lots Availability</h2>
        <p>Last Updated: {this.state.lastupdate}</p>
        <div style={{overflow: 'auto', maxHeight: 500}}>
          <ReactList
            itemRenderer={this.renderItem.bind(this)}
            length={this.state.carpark.length}
            type='uniform'
          />
        </div>
      </div>

    );
  }

}


export default App;
